@import "../../global.scss";

.intro {
    background-color: $bgmain;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 600px;
            height: 600px;
            background-color: $secColor;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;
            overflow: hidden;

            @include mobile{
                align-items: flex-start;
            }

            img{
                height:130%;
                transform: translate(150px,0);
                @include mobile{
                    transform: translate(150px,-200px);
                }

            }
        }
    }
    .right{
        flex: 0.5;
        position: relative;
        .wrapper{
            width: 100%;
            height:100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
            }

            h1{
                font-size: 60px;
                margin: 10px 0px;

                @include mobile{
                    font-size: 40px;
                }
            }
            h2{
                font-size: 30px;

                @include mobile{
                    font-size: 25px;
                }
            }
            h3{
                font-size: 29px;
                span{
                    font-size: inherit;
                    color: $secColor;
                }
                @include mobile{
                    font-size: 20px;
                }
            }

        }
        a{
            position: absolute;
            bottom: 10px;
            left: 35%;

            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }
            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
            }
            @include mobile{
                display: flex;
                align-items: center;
            }
        }
    }
}