@import "../../global.scss";

.menu{
    max-width: 300px;
    min-height: 100vh;
    background-color: $mainColor;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 0;
    right: -300px; 
    z-index: 2;
    transition: all 1s ease;

    &.active{
        right:0;
    }

    ul{
        margin:0;
        padding:0;
        list-style: none;
        font-size: 30px;
        font-weight: 200;
        color: white;
        width: 60%;
        
        li{
            margin-bottom: 25px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none; 
            }
            &:hover{
                font-weight: 500;
            }
        }
    }
}